// react
import React from 'react';

//third-party
import {connect} from 'react-redux'
import { Link } from 'react-router-dom';


// application
import FooterContacts from './FooterContacts';
import FooterLinks from './FooterLinks';
import FooterNewsletter from './FooterNewsletter';
import ToTop from './ToTop';



function Footer ( props ) {
    const { userLogin: { userInfo } } = props;
    
    const informationLinks = [
        { title: 'Delivery Information', url: '' },
        { title: 'Privacy Policy', url: '' },
        { title: 'Contact Us', url: '' },
        { title: 'Returns', url: '' },
    ];

    const informationLinks_ar = [
        { title: ' ', url: '/shop/track-order' },
        { title: ' ', url: '/site/privacy' },
        { title: ' ', url: '/site/contact-us' },
        { title: ' ', url: '/site/terms' },
    ];

    let accountLinks;
    if ( userInfo ) {
        accountLinks = [
        { title: 'Dashboard', url: '/account/dashboard' },
        { title: 'Edit Profile', url: '/account/profile' },
        { title: 'Order History', url: '/account/orders' },
        { title: 'Addresses', url: '/account/addresses' },
        { title: 'Password', url: '/account/password' },
    ];
    } else {
        accountLinks = [
        { title: 'Login', url: '/account/login' },
        { title: 'Sign up', url: '/account/signup' }
    ];
    }

    let accountLinks_ar;
    if ( userInfo ) {
        accountLinks_ar = [
        { title: '  ', url: '/account/dashboard' },
        { title: ' ', url: '/account/profile' },
        { title: ' ', url: '/account/orders' },
        { title: '', url: '/account/addresses' },
        // { title: ' ', url: '/account/password' },
    ];
    } else {
        accountLinks_ar = [
        { title: ' ', url: '/account/login' },
        { title: '', url: '/account/signup' }
    ];
    }

    return (
        <div className="site-footer">
            <div className="container">
                <div className="site-footer__widgets">
                    <div className="row">
                        <div className="col-12 col-md-6 col-lg-4">
                            <FooterContacts />
                        </div>
                        <div className="col-6 col-md-3 col-lg-2">
                            {/* <FooterLinks title="Information" items={informationLinks} /> */}
                            <FooterLinks title=" " items={informationLinks_ar} />
                        </div>
                        <div className="col-6 col-md-3 col-lg-2">
                            <FooterLinks title="" items={accountLinks_ar} />
                        </div>
                        <div className="col-12 col-md-12 col-lg-4">
                            <FooterNewsletter />
                        </div>
                    </div>
                </div>

                <div className="site-footer__bottom">
                    <div className="site-footer__copyright">
                        stroyka 2022
                        {' '}
                        
                        &copy;
                          
                    </div>
                    <div className="site-footer__payments">
                         <img src="/uploads/imgs/visa.png" alt="visa" style={{width:'40px',margin:"0 10px"}}/>
                            <img src="/uploads/imgs/master.png" alt="visa" style={{width:'40px',margin:"0 10px"}}/>
                            <img src="/uploads/imgs/meza.png" alt="visa" style={{width:'40px',margin:"0 10px"}}/>
                    </div>
                </div>
                
            </div>
            <ToTop />
        </div>
    );
}

const mapStateToProps = (state) => ({
    userLogin:state.userLogin
});

export default  connect(mapStateToProps)(Footer)
